








































import { Component, PropSync, Emit, Watch, Vue } from 'vue-property-decorator';

@Component({})
export default class UI extends Vue {

	public speed: number = 0.02;

	@Watch('speed')
	@Emit('onSpeedChanged')
	private speedChanged(newSpeed: number, oldSpeed: number): number {
		return this.speed;
	}
}

