







































/**
 * Test1.vue
 * - PropSyncの使い方を学ぶ
 * - .syncで受けて記述減らす
 */

import { Component, PropSync, Emit, Watch, Vue } from 'vue-property-decorator';

@Component({})
export default class Test1 extends Vue {

	@PropSync('syncedSpeed', {type: Number})
	private speed!: number;

}

